function getElementsToUpdate(input) {
    return [...document.querySelectorAll(`[data-updates-by="#${input.id}"]`)];
}

function getTotalPrice() {
    const inputs = [...document.querySelectorAll("#order-form input")];

    return inputs
        .filter(item => item.hasAttribute("data-base-price"))
        .map(input => {
            const basePrice = parseFloat(input.getAttribute("data-base-price"));

            if (input.type === "radio") {
                return input.checked ? basePrice : 0;
            } else if (input.type === "number") {
					if ((input.id == "order-form-rooms") || (input.id == "order-form-bathrooms")){
						
						if (input.value <= 1){
							return 0;
						}
						else{
							return (parseInt(input.value) - 1) * basePrice;
						}
					}
					else{ 
						return parseInt(input.value) * basePrice;
					}
                
            }
        })
        .reduce((result, current) => result + current);
}

function getItemPrice(input) {
    // It format is "#form-choice-3", with "dash" (#) symbol
    const dependencyId = input.getAttribute("data-depends-on");

    // If click was from dependency
    // We shall find it's "parent" and calc all the stuff from it
    if (dependencyId) {
        input = document.querySelector(dependencyId);
    }

    const inputId = input.id;

    if (inputId) {
        const inputValue = parseInt(input.value);
        const inputBasePrice = parseFloat(input.getAttribute("data-base-price"));

        // If 0 or 1 selected -- price will be same as for 1
        let sum = inputBasePrice * (inputValue > 1 ? inputValue : 1);

        const dependings = [...document.querySelectorAll(`[data-depends-on="#${inputId}"]`)]

        dependings.forEach(inputNode => {
            const inputValue = parseInt(inputNode.value);
            const inputBasePrice = parseFloat(inputNode.getAttribute("data-base-price"));

            // If 0 selected -- 0 will be after calculating
            sum += inputBasePrice * inputValue;
        });

        return sum;
    }
}

function renderUpdate(input, nodesList) {
    const count = parseInt(input.value);

    nodesList.forEach(node => {
        if (count) {
            node.classList.add("checked");
        } else {
            node.classList.remove("checked");
        }

        if (node.classList.contains("counter-item-count")) {
            node.value = input.value;
        }
    });
}

function updateState(input, nodesList) {
    if (input.type === "number") {
        const count = parseInt(input.value);
        renderUpdate(input, nodesList);

        const dependsOn = input.getAttribute("data-depends-on");
        const dependsOf = [...document.querySelectorAll(`[data-depends-on="#${input.id}"]`)];

        // Dependency was clicked
        if (dependsOn) {
            const dependsOnInput = document.querySelector(`${dependsOn}`);

            if (!parseInt(dependsOnInput.value)) {
                dependsOnInput.value = count ? 1 : 0;
                // We're not dispatch "change" Event to prevent infinite recursion
                // Just call update manually
                renderUpdate(dependsOnInput, getElementsToUpdate(dependsOnInput));
            }
        }

        // If parent was clicked
        if (dependsOf.length) {
            if (parseInt(input.value) === 0) {
                dependsOf.forEach(inputNode => {
                    inputNode.value = input.value;
                    inputNode.dispatchEvent(new Event("change"));
                });
            }
        }
    } else if (input.type === "radio") {
        const isChecked = input.checked;

        nodesList.forEach(node => {
            if (isChecked) {
                node.classList.add("checked");
            } else {
                node.classList.remove("checked");
            }
        });
    }

    renderTotalPrice();
    renderItemPrice(input);
}

function renderRadiosUpdate(input) {
    const sameInputs = [...document.querySelectorAll(`#order-form input[name=${input.name}]`)];

    sameInputs.forEach(input => {
        updateState(input, getElementsToUpdate(input));
    });
}

function renderInputUpdate(input) {
    if (!input.id) {
        return null;
    }

    updateState(input, getElementsToUpdate(input));
}

function renderTotalPrice() {
    const totalPrice = getTotalPrice().toFixed(0);
    const priceBlocks = [...document.querySelectorAll("[data-watch-price]")];

    priceBlocks.forEach(block => $(block).numerator({toValue: totalPrice, duration: 200}));
	 $(".status-bar.fixed").addClass("fadeOut");
	 setTimeout(function(){
		 $(".status-bar.fixed").removeClass("fadeOut");
	 }, 300);
}

function renderItemPrice(input) {
    const itemPrice = getItemPrice(input).toFixed(0);

    // It format is "#form-choice-3", with "dash" (#) symbol
    const dependencyId = input.getAttribute("data-depends-on");

    // If click was from dependency
    // We shall find it's "parent" and calc all the stuff from it
    if (dependencyId) {
        input = document.querySelector(dependencyId);
    }

    const inputId = input.id;


    const nodesToUpdate = [...document.querySelectorAll(`[data-watch-price-of="#${inputId}"]`)];
    nodesToUpdate.forEach(node => $(node).numerator({toValue: itemPrice, duration: 200}));
}

function doDecrement(item) {
    const input = document.querySelector(item.getAttribute("data-decrement-on"));

    return function (event) {
        event.stopPropagation();
        event.preventDefault();

        if (parseInt(input.value)) {
            
				
				if (((input.id == "order-form-rooms") || (input.id == "order-form-bathrooms"))){
					$(".choice--main").each(function(){
						var textCurrency = $(this).find('.choice-price span').text(),
							 commonPrice = parseInt(input.getAttribute("data-base-price")),
							 currentPrice = parseInt(textCurrency.slice(0, -2)),
							 newPrice = currentPrice;
						if ((parseInt(input.value) > 1)){
							 newPrice = currentPrice - commonPrice;
						} 
						textCurrency = textCurrency.slice(textCurrency.length - 2,textCurrency.length);
						$(this).find('.choice-price span').text(newPrice + textCurrency);
					});
				}
			
				if (((input.id == "order-form-rooms") || (input.id == "order-form-bathrooms"))
					&& (parseInt(input.value) <= 1)){
					input.value = 1;
					
					
				}
				else{
					input.value = parseInt(input.value) - 1;
				}
            input.dispatchEvent(new Event("change"));
					
				
				
				
        }
    }
}

function doIncrement(item) {
    const input = document.querySelector(item.getAttribute("data-increment-on"));

    return function (event) {
        event.stopPropagation();
        event.preventDefault();

        input.value = parseInt(input.value) + 1;
		 
		  	if (((input.id == "order-form-rooms") || (input.id == "order-form-bathrooms"))){
				$(".choice--main").each(function(){
					var textCurrency = $(this).find('.choice-price span').text(),
						 commonPrice = parseInt(input.getAttribute("data-base-price")),
						 currentPrice = parseInt(textCurrency.slice(0, -2)),
						 newPrice = currentPrice;
					if ((parseInt(input.value) > 1)){
						 newPrice = currentPrice + commonPrice;
					}
					textCurrency = textCurrency.slice(textCurrency.length - 2,textCurrency.length);
					$(this).find('.choice-price span').text(newPrice + textCurrency);
				});
			}  
		
		  
		  
		  if (input)
        input.dispatchEvent(new Event("change"));
	  
		
    }
}


module.exports = function () {
    const inputs = [...document.querySelectorAll("#order-form input")];

    function init(inputs) {
        const decrements = [...document.querySelectorAll("[data-decrement-on]")];
        const increments = [...document.querySelectorAll("[data-increment-on]")];

        decrements.forEach(item => item.addEventListener("click", doDecrement(item)));
        increments.forEach(item => item.addEventListener("click", doIncrement(item)));

        inputs.forEach(input => {
            if (!input.id) {
                return null;
            }

            const elementsToUpdate = getElementsToUpdate(input);

            elementsToUpdate.forEach(node => {
                const updatesBy = node.getAttribute("data-updates-by");
                const updatesByInput = document.querySelector(updatesBy);

                if (node.classList.contains("counter-item-count")) {
                    node.addEventListener("keyup", function (event) {
                        event.stopPropagation();
                        event.preventDefault();

                        updatesByInput.value = this.value;
                        updatesByInput.dispatchEvent(new Event("change"));
                    });
                }

                node.addEventListener("click", function (event) {
						  if (!$(event.target).hasClass("tooltip")){
							  event.stopPropagation();
							  event.preventDefault();

							  if (this.classList.contains("your-order-item")) {
									const isDeleteWasClicked = (
										 event.target.classList.contains("your-order-item-delete")
										 || event.target.classList.contains("your-order-item-delete__icon")
									);

									if (isDeleteWasClicked) {
										 this._tippy.hide();
										 if (updatesByInput.type === "number") {
											  updatesByInput.value = parseInt(updatesByInput.value) ? 0 : 1;
											  updatesByInput.dispatchEvent(new Event("change"));
										 }
									}

									return null;
							  }

							  if (this.classList.contains("counter-item-count")) {
									return null;
							  }

							  if (updatesByInput.type === "number") {
									updatesByInput.value = parseInt(updatesByInput.value) ? 0 : 1;
									updatesByInput.dispatchEvent(new Event("change"));
							  } else if (updatesByInput.type === "radio") {
									updatesByInput.checked = true;
									renderRadiosUpdate(updatesByInput);
							  }
						  }
                });
            });

            if (input.type === "radio") {
                input.addEventListener("change", function () {
                    renderRadiosUpdate(this);
                });
            } else {
                input.addEventListener("change", function () {
                    renderInputUpdate(this);
                });
            }

            // Render on page load
            renderInputUpdate(input);
        });
    }

    init(inputs);
};

$('.choice--main').on('click', function (e) {
	var w = $(window).width();
	if ((!$(e.target).hasClass("tooltip")) && (w <= 500)){
		$("html, body").animate({scrollTop: $(".choices-section").offset().top - $(".status-bar").height() - 50}, 800);
	}
  if (!$(this).hasClass("checked")){
	  $(".choices-section .choices-item.checked").trigger("click");
  }
  
  if (!$(e.target).hasClass("tooltip")){
	  $(".status-bar-message").removeClass("active");
	  $(".make-order-footer .status-bar-submit").removeClass("active");
		$(".status-bar.fixed").addClass("active");
  }
  
  
	
});


